/* Header.css */

.header {
    background-color: #ecf0f1;
    padding-top: 100px;
    min-height: 100vh;
    display: flex;
    align-items: center;
}

.header-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.header-text {
    flex: 1;
    margin-right: 20px;
}

.header-text h1 {
    font-family: 'Poppins', sans-serif;
    font-size: 48px;
    color: #2c3e50;
    margin-bottom: 20px;
}

.header-text h1 span {
    color: #1abc9c;
}

.header-text p {
    font-size: 18px;
    margin-bottom: 30px;
    color: #34495e;
}

.btn {
    display: inline-block;
    padding: 15px 30px;
    font-size: 18px;
    color: #fff;
    background: linear-gradient(45deg, #1abc9c, #16a085);
    border: none;
    border-radius: 30px;
    text-decoration: none;
    transition: background 0.3s;
}

.btn:hover {
    background: linear-gradient(45deg, #16a085, #1abc9c);
}

.header-image {
    flex: 1;
    text-align: center;
}

.header-image img {
    width: 100%;
    max-width: 500px;
    border-radius: 20px;
    box-shadow: 0 10px 25px rgba(0,0,0,0.1);
    transition: transform 0.3s;
}

.header-image img:hover {
    transform: scale(1.05);
}

@media (max-width: 768px) {
    .header-content {
        flex-direction: column;
        text-align: center;
    }
}