/* Section.css */

section {
    padding: 100px 0;
}

.section-content {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.section-content.reverse {
    flex-direction: row-reverse;
}

.section-text {
    flex: 1;
    margin: 20px;
}

.section-text h2 {
    font-family: 'Poppins', sans-serif;
    font-size: 36px;
    color: #2c3e50;
    margin-bottom: 20px;
}

.section-text p, .section-text ul {
    font-size: 18px;
    color: #34495e;
}

.section-text ul {
    list-style-type: disc;
    padding-left: 20px;
}

.section-image {
    flex: 1;
    text-align: center;
    margin: 20px;
}

.section-image img {
    width: 100%;
    max-width: 500px;
    border-radius: 20px;
    box-shadow: 0 10px 25px rgba(0,0,0,0.1);
    transition: transform 0.3s;
}

.section-image img:hover {
    transform: scale(1.05);
}

/* Responsive Styles */
@media (max-width: 768px) {
    .section-content {
        flex-direction: column;
        text-align: center;
    }
}
