/* Navbar.css */

/* Navigation Styles */
nav {
    width: 100%;
    background-color: #1b2a49;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.nav-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
}

.logo {
    font-size: 24px;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
}

#menu-links {
    list-style: none;
    display: flex;
}

#menu-links li {
    margin-left: 30px;
}

#menu-links li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    transition: color 0.3s;
}

#menu-links li a:hover {
    color: #1abc9c;
}

.menu-icon {
    display: none;
    flex-direction: column;
    cursor: pointer;
}

.menu-icon span {
    width: 25px;
    height: 3px;
    background-color: #fff;
    margin: 4px 0;
    transition: 0.4s;
}

/* Responsive Styles */
@media (max-width: 768px) {
    #menu-links {
        position: absolute;
        top: 60px;
        right: 0;
        background-color: #1b2a49;
        width: 100%;
        height: 0;
        overflow: scroll;
        flex-direction: column;
        align-items: center;
        transition: height 0.3s;
    }

    #menu-links.active {
        height: 200px;
    }

    #menu-links li {
        margin: 15px 0;
    }

    .menu-icon {
        display: flex;
    }
}
