/* Contact.css */

#contact {
    padding: 100px 5%;
    background-color: #1e1e1e;
    color: #f5f5f5;
    text-align: center;
}

#contact h2 {
    font-size: 50px;
    margin-bottom: 30px;
    color: #fada0c;
}

#contact p {
    font-size: 20px;
    margin-bottom: 20px;
}

.social-icons {
    display: flex;
    justify-content: center;
    gap: 40px;
}

.social-link {
    font-size: 60px;
    color: #f5f5f5;
    transition: transform 0.3s, color 0.3s;
}

.social-link:hover {
    transform: scale(1.3);
}

/* Specific color schemes for each icon */
.facebook:hover {
    color: #3b5998;
}

.instagram:hover {
    color: #e1306c;
}

.tiktok:hover {
    color: #000000;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .social-icons {
        gap: 20px;
    }

    .social-link {
        font-size: 40px;
    }
}
